<template>
  <v-container grid-list-md>
    
      <v-card>
        <v-card-title>
          Ваши клиенты
          <v-layout row>
            <v-dialog v-model="dialog" max-width="800px" persistent>
              <template v-slot:activator="{on}">
                <v-btn icon v-on="on">
                  <v-icon>mdi-plus-circle-outline</v-icon>
                </v-btn>
              </template>
              <v-card>
                <v-card-title>
                  <span class="headline" v-if="!edit">Добавление клиента</span>
                </v-card-title>
                <v-card-text>
                  <v-container grid-list-md>
                    
                      <v-form
                       v-model="valid"
                       ref="form"
                      >
                      <v-layout wrap>
                        <v-flex xs4>
                        <v-text-field label="Клиент*" required v-model="name" :rules="rules" counter="15"></v-text-field>
                      </v-flex>
                      <v-flex xs4>
                        <v-text-field label="E-mail*" required v-model="email" :rules="rules" counter="15"></v-text-field>
                      </v-flex>
                      <v-flex xs4>
                        <v-text-field label="Телефон*" required v-model="phone" v-mask="maskPhone" :rules="[
                          v => !!v || 'Поле не должно быть пустым',
                          v => (v && v.length === 15 || 'Телефон не полный')
                        ]"></v-text-field>
                      </v-flex>
                      </v-layout>
                      </v-form>
                    
                  </v-container>
                  <small>* обязательные поля</small>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="zeroing">Закрыть</v-btn>
                  <v-btn color="blue darken-1" text @click="addClients" v-if="!edit" :disabled="!valid">Добавить</v-btn>
                  <v-btn color="blue darken-1" text @click="addClients" v-else>Сохранить</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-layout>
          <v-spacer></v-spacer>
          <v-text-field v-model="search" append-icon="mdi-magnify" label="Поиск" single-line hide-details>
          </v-text-field>
        </v-card-title>
        <v-data-table :headers="headers" :items="clients" :search="search" :items-per-page="10" :page.sync="page"
          hide-default-footer @page-count="pageCount = $event" />
        <v-card-actions>
          <v-pagination v-model="page" :length="pageCount"></v-pagination>
        </v-card-actions>
      </v-card>
  
  </v-container>
</template>

<script>
  import {
    mask
  } from 'vue-the-mask'
  export default {
    directives: {
      mask
    },
    data() {
      return {
        valid: false,
        rules: [
          v => (v && v.length <= 15) || 'Максимум 15 символов',
          v => !!v || 'Поле не должно быть пустым'
        ],
        maskPhone: '#-###-###-##-##',
        value: '8-888-888-88-88',
        dialog: false,
        edit: false,
        page: 1,
        pageCount: 0,
        search: '',
        headers: [{
            text: 'Клиент',
            value: 'name'
          },
          {
            text: 'E-mail',
            value: 'email',
            sortable: false
          },
          {
            text: 'Телефон',
            value: 'phone',
            sortable: false
          }
        ],
        name: '',
        email: '',
        phone: ''
      }
    },
    methods: {
      addClients() {
        this.$store.dispatch('addClient', {
          name: this.name,
          email: this.email,
          phone: this.phone
        })
        this.zeroing()
      },
      zeroing() {
        this.dialog = false,
        this.edit = false,
        this.name = '',
        this.email = '',
        this.phone = ''
        this.$refs.form.resetValidation()
      }
    },
    computed: {
      clients() {
        return this.$store.getters.getClientsList
      },
    }
  }
</script>